import React from "react";
import {
  createSmartappDebugger,
  createAssistant,
} from "@sberdevices/assistant-client";
import { createGlobalStyle } from "styled-components";
import { darkJoy, darkEva, darkSber } from "@sberdevices/plasma-tokens/themes";
import { text, background, gradient } from "@sberdevices/plasma-tokens";
import { DeviceThemeProvider } from "@sberdevices/plasma-ui/components/Device";
import "./App.css";
import Chat from "../Chat_cmp/Chat";
import Statistic from "../Statistic_cmp/Statistic";
import { EndGame } from "../EndGame_cmp/EndGame";
import { Help } from "../Help_cmp/Help"
import { findNickName, sendNickName, clearRequest } from "../../service/API_helper";
import { toNameFormat } from "../../service/WordChecker";
const rules = `–û–¥–∏–Ω –∏–≥—Ä–æ–∫ –≥–æ–≤–æ—Ä–∏—Ç –∏–º—è, 
                –∞ –¥—Ä—É–≥–æ–π –æ—Ç–≤–µ—á–∞–µ—Ç –µ–º—É –∏–º–µ–Ω–µ–º –Ω–∞ –ø–æ—Å–ª–µ–¥–Ω—é—é –±—É–∫–≤—É. –ù–∞ –æ—Ç–≤–µ—Ç –¥–∞–µ—Ç—Å—è 30 —Å–µ–∫—É–Ω–¥, 
                –ø–æ—Å–ª–µ —á–µ–≥–æ –∏–≥—Ä–∞ –∑–∞–∫–∞–Ω—á–∏–≤–∞–µ—Ç—Å—è. –ú–æ–∂–Ω–æ —Å–º–µ–Ω–∏—Ç—å —Å–≤–æ–µ –∏–≥—Ä–æ–≤–æ–µ –∏–º—è, —Å–∫–∞–∑–∞–≤ 
                "–ò–≥—Ä–æ–≤–æ–µ –∏–º—è" –∏–ª–∏ "–ù–∏–∫". –ß—Ç–æ–±—ã –ø–µ—Ä–µ–∑–∞–ø—É—Å—Ç–∏—Ç—å –∏–≥—Ä—É, —Å–∫–∞–∂–∏—Ç–µ "–∑–∞–Ω–æ–≤–æ" –∏–ª–∏ "—Å–Ω–∞—á–∞–ª–∞". 
                –ß—Ç–æ–±—ã –ø—Ä–∏–æ—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –∏–≥—Ä—É, —Å–∫–∞–∂–∏—Ç–µ "–ø–∞—É–∑–∞". –ü–æ—è–≤–∏—Ç—Å—è –∑–Ω–∞—á–æ–∫ —á–∞—Å–æ–≤. –û–Ω–∞ –∞–∫—Ç–∏–≤–∏—Ä—É–µ—Ç—Å—è 
                —Ç–æ–ª—å–∫–æ –µ—Å–ª–∏ –ø–æ—Å–ª–µ–¥–Ω–µ–µ —Å–∫–∞–∑–∞–Ω–Ω–æ–µ —Å–ª–æ–≤–æ –±—ã–ª–æ –∑–∞ –∏–≥—Ä–æ–∫–æ–º. –ß—Ç–æ–±—ã –Ω–∞—á–∞—Ç—å –∏–≥—Ä–∞—Ç—å, —Å–∫–∞–∂–∏—Ç–µ
                "–Ω–∞—á–∞—Ç—å".`
    
const ThemeBackgroundEva = createGlobalStyle(darkEva);
const ThemeBackgroundSber = createGlobalStyle(darkSber);
const ThemeBackgroundJoy = createGlobalStyle(darkJoy);

const initializeAssistant = (getState) => {
  if (process.env.NODE_ENV === "development") {
    return createSmartappDebugger({
      token: process.env.REACT_APP_TOKEN ?? "",
      initPhrase: `–ó–∞–ø—É—Å—Ç–∏ ${process.env.REACT_APP_SMARTAPP}`,
      getState,
    });
  }
  return createAssistant({ getState });
};

const DocStyle = createGlobalStyle`
    html:root {
        min-height: 100vh;
        color: ${text};
        background-color: ${background};
        background-image: ${gradient};
    }
`;

let newName = ""; //–∏–º—è, —Å–∫–∞–∑–∞–Ω–Ω–æ–µ –≥–æ–ª–æ—Å–æ–º –∞—Å—Å–∏—Å—Ç–µ–Ω—Ç—É
let userId = "default";
let helpCalled = false;
let gameStart = false;

export class App extends React.Component {
  constructor(props) {
    super(props);
    console.log("constructor")
    this.state = {
      messages: [], //—Å–∫–∞–∑–∞–Ω–Ω—ã–µ –∏–º–µ–Ω–∞
      nameCount: 0, //–∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–∫–∞–∑–∞–Ω–Ω—ã—Ö –∏–º–µ–Ω
      character: "sber", //—Ç–µ–∫—É—â–∏–π –ø–µ—Ä—Å–æ–Ω–∞–∂
      isEndGame: false, //–æ–±–Ω–æ–≤–ª—è–µ—Ç—Å—è, –∫–æ–≥–¥–∞ –∏–≥—Ä–∞ –∑–∞–∫–∞–Ω—á–∏–≤–∞–µ—Ç—Å—è
      restarted: false, //–∏–∑–º–µ–Ω—è–µ—Ç—Å—è, –∫–æ–≥–¥–∞ –Ω—É–∂–µ–Ω —Ä–µ—Å—Ç–∞—Ä—Ç –∏–≥—Ä—ã
      timerUpdate: false, //–∏–∑–º–µ–Ω—è–µ—Ç—Å—è –∫–æ–≥–¥–∞ –Ω–∞–¥–æ –æ–±–Ω–æ–≤–∏—Ç—å —Ç–∞–π–º–µ—Ä
      playerWin: false, //true, –µ—Å–ª–∏ –ø–æ—Å–ª–µ–¥–Ω–µ–µ —Å–ª–æ–≤–æ —Å–∫–∞–∑–∞–Ω–æ –∏–≥—Ä–æ–∫–æ–º
      isPause: true, //true, –µ—Å–ª–∏ –Ω–∞–∂–∞—Ç–∞ –ø–∞—É–∑–∞
      pauseAllow: true, //true, –µ—Å–ª–∏ –≤ –¥–∞–Ω–Ω—ã–π –º–æ–º–µ–Ω—Ç –º–æ–∂–Ω–æ –æ—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –∏–≥—Ä—É
      existPauseRequest: true,
      assistantSayTime: -1, //–Ω–∞ –∫–∞–∫–æ–π —Å–µ–∫—É–Ω–¥–µ –∞—Å—Å–∏—Å—Ç–µ–Ω—Ç –¥–æ–ª–∂–µ–Ω —Å–∫–∞–∑–∞—Ç—å –∏–º—è
      assistantSay: false, //true, –µ—Å–ª–∏ —Å–µ–π—á–∞—Å —Ö–æ–¥ –∞—Å—Å–∏—Å—Ç–µ–Ω—Ç–∞
      nickname: "–ò–≥—Ä–æ–∫", //–Ω–∏–∫–Ω–µ–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
    };

    this.assistant = initializeAssistant(() => this.getStateForAssistant());
    this.assistant.on("start", (event) => {
      this.assistant.sendData({
        action: {
          action_id: "newGame",
        },
      });
      console.log(`assistant.on(start)`, event);
      setTimeout(this.assistant.sendData({action: {action_id: "getSub",}}), 300);
    });

    this.assistant.on("data", (event) => {
      switch (event.type) {
        case "character":
          if (event.character.id === "eva") newName = "–ê—Ñ–∏–Ω–∞";
          if (event.character.id === "joy") newName = "–î–∂–æ–π";
          this.setState({
            character: event.character.id,
          });
          break;
        case "close_app":
          clearRequest(userId);
          break;
      }
      const { action } = event;
      this.dispatchAssistantAction(action);
    });
  }
  
  getStateForAssistant() {
    const state = {
      item_selector: {
        items: this.state.messages.map(({ name, from }, index) => ({
          name,
          from,
        })),
      },
    };
    return state;
  }
  //–ü–æ–ª—É—á–µ–Ω–∏–µ/–æ—Ç–ø—Ä–∞–≤–∫–∞ –Ω–∏–∫–Ω–µ–π–º–∞ –≤ –±—ç–∫–µ–Ω–¥
  async dispatchAssistantAction(action) {
    console.log("dispatchAssistantAction", action);
    if (action) {
      switch (action.type) {
        case "get_sub":
          userId = action.data;
          let { data } = await findNickName(userId);
          this.setState({nickname: data});
          break;
        case "add_nickname":
          let newNick = await toNameFormat(action.data);
          console.log("–ò–≥—Ä–æ–≤–æ–µ –∏–º—è: " + newNick);
          if (newNick.length < 15) {
            console.log("Correct nick");
            await sendNickName(userId, newNick);
            this.setState({ nickname: newNick });
          } else {
            this.assistant.sendData({
              action: {
                action_id: "repeat",
              },
            });
          }
          break;
        case "add_name":
          newName = action.data;
          this.setState(this.state);
          break;
        case "restart_game":
          this.restart();
          break;
        case "pause_game":
          if(!this.state.isPause && !this.state.isEndGame && !helpCalled )
            this.setState({
              existPauseRequest: !this.state.existPauseRequest,
            });
          break;
        case "continue_game":
          if (this.state.isPause && !this.state.isEndGame && !helpCalled)           
            this.setState({
              existPauseRequest: !this.state.existPauseRequest,
            });
          break;
        case "start_game":
          if(!gameStart && !helpCalled) {
            console.log(this.state.existPauseRequest)
            this.setState({
              existPauseRequest: false
            })
          }
          break;
        case "rules_game":
          if(!helpCalled && !this.state.isEndGame) {
            this.help();
          }
          break;
        case "close_rules":
          if(this.state.isPause && helpCalled) {
            helpCalled = false;
            this.setState(this.state);
          }
          break;
        default:
          break;
      }
    }
  }

  endGame = async () => {
    gameStart = false;
    this.setState({
      isEndGame: true,
      isPause: true,
    });
    this.assistant.sendData({
      action: {
        action_id: this.state.playerWin ? "assistantLose" : "assistantWin",
      },
    });
    await clearRequest(userId);
  };

  restart = async () => {
    console.log("restart");
    newName = "";
    helpCalled = false;
    gameStart = true;
    this.setState({
      messages: [],
      nameCount: 0,
      isEndGame: false,
      restarted: !this.state.restarted,
      timerUpdate: !this.state.timerUpdate,
      playerWin: false,
      isPause: false,
      pauseAllow: true,
      assistantSayTime: -1,
      assistantSay: false,
      existPauseRequest: false,
    });
    await clearRequest(userId);
  };

  exit = async () => {
    await clearRequest(userId);
    this.assistant.close();
  };

  pause = () => {
    if(!gameStart) gameStart = true;
      if(helpCalled) helpCalled = false;
      this.setState({
        isPause: !this.state.isPause,
      });
  };

  update = (verdict) => {
    this.setState({
      nameCount: this.state.nameCount + 1,
      timerUpdate: !this.state.timerUpdate,
      playerWin: verdict,
      pauseAllow: verdict,
    });
  };

  help = () => {
    if(this.state.isPause) {
      helpCalled = !helpCalled;
      if(helpCalled) {
        this.assistant.sendData({
          action: {
            action_id: "Help",
            parameters: { rules: rules }
          }
        })
      }
    }
    this.setState(this.state);
  }
  pauseRequest = () => {
    this.setState({
      existPauseRequest: !this.state.existPauseRequest
    })
  };

  assistantSay = () => {
    this.setState({
      assistantSay: true,
      assistantSayTime: -1,
    });
  };

  assistantSaied = () => {
    this.setState({
      assistantSay: false,
    });
  };

  setAssistantSayTime = (sec) => {
    this.setState({
      assistantSayTime: sec,
    });
  };

  render() {
    return (
      <DeviceThemeProvider>
        <DocStyle />
        {(() => {
          switch (this.state.character) {
            case "sber":
              return <ThemeBackgroundSber />;
            case "eva":
              return <ThemeBackgroundEva />;
            case "joy":
              return <ThemeBackgroundJoy />;
            default:
              return;
          }
        })()}
        <Help 
          helpCalled={helpCalled}
          isPause={this.state.isPause}
          rules={rules}
          help={this.help}
        />
        <div
          className="fill-container"
          style={{ display: this.state.isEndGame ? "flex" : "none" }}
        >
          {this.state.isEndGame ? (
            <EndGame
              count={this.state.nameCount}
              restart={this.restart}
              assistant={this.assistant}
              character={this.state.character}
              isWin={this.state.playerWin}
              isEndGame={this.state.isEndGame}
              exit={this.exit}
            />
          ) : (
            <div />
          )}
        </div>
        <div className="main-container">
          <Chat
            newname={newName}
            update={this.update}
            assistant={this.assistant}
            messages={this.state.messages}
            restarted={this.state.restarted}
            isPause={this.state.isPause}
            setAssistantSayTime={this.setAssistantSayTime}
            assistantSaied={this.assistantSaied}
            assistantSay={this.state.assistantSay}
            userId={userId}
            endGame={this.endGame}
          />
          <div className="statistic-container">
            <Statistic
              character={this.state.character}
              count={this.state.nameCount}
              endGame={this.endGame}
              restart={this.restart}
              pause={this.pause}
              exit={this.exit}
              help={this.help}
              isPause={this.state.isPause}
              allowPause={this.state.pauseAllow}
              pauseRequest={this.pauseRequest}
              update={this.state.timerUpdate}
              assistant={this.assistant}
              assistantSayTime={this.state.assistantSayTime}
              assistantSay={this.assistantSay}
              nickname={this.state.nickname}
              existPauseRequest={this.state.existPauseRequest}
              isWin={this.state.playerWin}
              restarted={this.state.restarted}
              isEndGame={this.state.isEndGame}
              helpCalled={helpCalled}
            />
          </div>
        </div>
      </DeviceThemeProvider>
    );
  }
}
